<template lang="jade">
#overlayer
 .layContent
  ul.cen_bind(v-show="$store.state.layer.binds[0].main_bind")
   v-touch.sec_close(tag="li",@tap="sec_close") 
   li
    label 
    input(v-model="$store.state.center.truename")
   li
    label ()
    input(v-model="$store.state.center.bank_account")
   li
    label 
    input(type="password",v-model="$store.state.center.bank_password")
   li
    label 
    select(v-model="$store.state.center.bankselect")
      option(value="7") 
      option(value="9") 
      option(value="11") 
      option(value="13") 
      option(value="15") 
      option(value="17") 
      option(value="19") 
      option(value="21") 
      option(value="23") 
      option(value="25") 
      option(value="27") 
      option(value="29") 
      option(value="31") 
      option(value="33") 
      option(value="83") 
      option(value="81") 
      option(value="79") 
      option(value="77") 
      option(value="73") 
      option(value="71") 
      option(value="69") 
      option(value="67") 
      option(value="63") 
      option(value="61") 
      option(value="59") 
      option(value="57") 
      option(value="55") 
      option(value="51") 
      option(value="49") 
      option(value="47") 
      option(value="45") 
      option(value="43") 
      option(value="41") 
      option(value="39") 
      option(value="37") 
      option(value="35") 
      option(value="85") 
      option(value="95") 
   li
    label 
    input(v-model="$store.state.center.bank_address")
   v-touch.binBtn(@tap="bankbind") 

  ul.cen_bind(v-show="$store.state.layer.binds[1].main_bind")
   v-touch.sec_close(tag="li",@tap="sec_close") 
   li
    label 
    input(v-model="$store.state.center.oldNewMoney",type="password")
   li
    label 
    input(v-model="$store.state.center.outNewMoney",type="password")
   li.secTip 4-12
   li
    label 
    input(v-model="$store.state.center.conNewMoney",type="password")
   v-touch.binBtn(@tap="outNewMoney") 
   //-
  ul.cen_bind(v-show="$store.state.layer.binds[2].main_bind")
   v-touch.sec_close(tag="li",@tap="sec_close") 
   li
    label 
    input(v-model="$store.state.center.oldPass",type="password")
   li
    label 
    input(v-model="newPass",type="password")
   li
    label 
    input(v-model="$store.state.center.newPass",type="password")
   v-touch.binBtn(@tap='changeloginpassword') 
  //-
  ul.cen_bind(v-show="$store.state.layer.binds[3].main_bind")
   v-touch.sec_close(tag="li",@tap="sec_close") 
   li
    label 
    input(v-model="$store.state.center.qwenumber")
   v-touch.binBtn(@tap='qewBind("email")') 
   //-QQ
  ul.cen_bind(v-show="$store.state.layer.binds[4].main_bind")
   v-touch.sec_close(tag="li",@tap="sec_close") 
   li
    label QQ
    input(v-model="$store.state.center.qwenumber")
   v-touch.binBtn(@tap='qewBind("qq")') 
   //-
  ul.cen_bind(v-show="$store.state.layer.binds[5].main_bind")
   v-touch.sec_close(tag="li",@tap="sec_close") 
   li
    label 
    input(v-model="$store.state.center.qwenumber")
   v-touch.binBtn(@tap='qewBind("wechat")') 
</template>
<script>
	export default{
    data(){
      return{
        newPass:""
      }
    }
    ,
	   mounted:function(){
	   	this.$nextTick(function(){
	   	})
	   },
	   methods:{
	   	 cen_firm(){
           this.$store.commit("close_cenlayer");
	   	 },
       bankbind(){
          this.$store.commit('bankBind');
       },
       sec_close(){
          this.$store.commit("sec_close");
       }
       ,
       qewBind(value){
         if(value=="email"){
           const reg = /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/;
           let testEmail =reg.test(this.$store.state.center.qwenumber);
            if(testEmail){
              this.$store.commit('qewBind',value);
            } else if(this.$store.state.center.qwenumber==""){
              this.$store.commit("centerTip",7);
            }else{
              this.$store.commit("centerTip",10);
            }
         }else if(value=="qq"){
           if(this.$store.state.center.qwenumber==""){
             this.$store.commit("centerTip",7);
           }else{
            this.$store.commit('qewBind',value);
           }
         }else if(value=="wechat"){
           if(this.$store.state.center.qwenumber==""){
             this.$store.commit("centerTip",7);
           }else{
             this.$store.commit('qewBind',value);
           }
         }
       },
       //
       outNewMoney(){
         let [password,cpword]=[this.$store.state.center.outNewMoney,this.$store.state.center.conNewMoney]
        const numReg=/^\d{4,12}$/g;
        const n=numReg.test(password);
        if(password!==''&&cpword!==''&&password!==cpword){
          this.$store.commit('registerTip','');
        }else if(password==""||cpword==""){
          this.$store.commit("centerTip",7);
        }else if(n){
          this.$store.commit("outNewMoney");
        }else{
          this.$store.commit('registerTip','');
        }

       },
       changeloginpassword(){
          const promise = new Promise((resolve, reject)=> {
            if(this.$store.state.center.oldPass==""||this.newPass==""||this.$store.state.center.newPass==""){
               resolve();
            }else{
              reject();
            }
          });
         promise.then(()=>{
           this.$store.commit("centerTip",7);
         },()=>{
             let password=/[\w]{8,16}$/g;
             var passwordz=password.test(this.newPass);
              if(!passwordz){
                this.$store.commit("centerTip",12);
              } else if(this.newPass!==this.$store.state.center.newPass){
                this.$store.commit("centerTip",13);
              }else{
                this.$store.commit("changeloginpassword");
                this.newPass=this.$store.state.center.newPass
              }
         })
       }
	   }
	}
</script>
<style lang="scss">
  @import "../../../assets/css/main.scss";
  .sec_close{
    width:rem2px(45);
    height:rem2px(43) !important;
    line-height:rem2px(48);
    border-radius:100%;
    color:#fff;
    text-align:center;
    background-color:$color_2;
    position:absolute;
    top:-0.4rem;
    right:-0.3rem;
  }
	#overlayer{
		width:100%;
		height:100%;
		background-color:rgba(0,0,0,0.25);
		position:fixed;
		z-index:9999;
	}
	.layContent{
	  @extend %font25;
		width:rem2px(500);
		background-color:#fff;
		position:relative;
		margin:0 auto;
		padding-top:0.8rem;
		padding-bottom:0.5rem;
    box-shadow:0rem 0.18rem 0.35rem rgba(0,0,0,0.25);
		top:5rem;
	  @extend %bz;
	}
	.cen_bind{
		width:80%;
		margin:0 auto;
		text-align:left;
    li:first-child{
      line-height:rem2px(40);
    }
		li{
			margin-bottom:0.3rem;
			height:rem2px(50);
      line-height:0.7rem;
			label{
				line-height:0.5rem;
			}
      input{
       float:right;
       border:1px solid #dedede;
       width:rem2px(200);
       height:rem2px(35);
       display:block;
			}
      select{
        float:right;
        width:3.55rem;
        height:0.6rem;
      }
		}
	}
	.binBtn{
		margin-top:0.5rem;
		width:rem2px(90);
		line-height:rem2px(35);
		background-color:$color_2;
		color:#fff;
		border-radius:0.1rem;
		text-align:center;
		@extend %m0a;
	}
  .secTip{
    height:0.5rem !important;
    margin:0!important;
    @extend %font24;
    @extend %bz;
    @extend %posr;
    top:-0.45rem;
    text-align:right;
    color:#ea4558;
  }
</style>



