import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import Userregister from './modules/register';
import layer from './modules/layer.js';
import center from './modules/center.js'
Vue.use(Vuex);
const userStore = new Vuex.Store({
	state: {
		url:"http://k3.net/",
		swiper:"",
		swiperGame:"",
		navToggle: true,
		headToggle: true,
		centerPage: false,
		centerTitle: '',
		paynowToggle:true,
		centers: [{
			toggle: false
		}, {
			toggle: false
		}, {
			toggle: false
		}, {
			toggle: false
		}, {
			toggle: false
		}, {
			toggle: false
		}, {
			toggle: false
		}, {
			toggle: false
		}],
		navs: [{
			toggle: true
		}, {
			toggle: false
		}, {
			toggle: false
		}, {
			toggle: false
		}],
		//
		navsGame:[{
			text:'',
			toggle:true
			},{
			text:'',
			toggle:false
			},{
			text:'',
			toggle:false
		  },{
			text:'',
			toggle:false
		}],
		reTitle: "",
		status: false,
		records: [{
			text: ""
		}, {
			text: ""
		}, {
			text: ""
		}, {
			text: ""
		}, {
			text: ""
		}],
		//,
		select2: false, //
		select_1: null,
		select_2: null,
		recordSelect_1: [],
		recordSelect_2: [],
		//
		recordDatas: [],
		selectData_1: 0, //select
		selectData_2: 0, //select
		startTime: null,
		endTime: null,
		page: 1,
		totalNumber: 1
	},
	mutations: {
		swiper(state, value) {
			state.swiper = value;
		},
		//$refs.mySwiper
		swipergame(state,value){
      state.swiperGame = value;
		},
		//gameswipergame
		swiperTo(state,value) {
			const nav=value.nav;
      const index=value.index
			if (state.register.loginStatus == 211003) {
				state.navs.forEach(function(nav) {
					Vue.set(nav, 'toggle', false)
				})
				Vue.set(nav, 'toggle', true)
			}else if(index!==3){
				state.navs.forEach(function(nav) {
					Vue.set(nav, 'toggle', false)
				})
				Vue.set(nav, 'toggle', true)
			}
		},
		swiperTopage(state, value) {
			if (value == 0) {
				state.swiper.slideTo(value, 500, false)
				state.swiper.slideTo(0, 500, false);
				Vue.set(state.navs[value], 'toggle', true)
				state.headToggle = true;
				state.navToggle = true;
			} else if (value == 1) {
				state.swiper.slideTo(value, 500, false)
				state.headToggle = false
			} else if (value == 2) {
				state.navs.forEach(function(nav) {
					Vue.set(nav, 'toggle', false)
				})
				state.swiper.slideTo(value, 500, false);
				Vue.set(state.navs[value], 'toggle', true)
				state.headToggle = false;
			} else if (value == 3 && state.register.loginStatus == 211003) {
				state.swiper.slideTo(3, 500, false)
				state.headToggle = true
			} else if (state.register.loginStatus !== 211003) {
				state.layer.main_message = ""
				state.layer.main_layer = !state.layer.main_layer
				setTimeout(() => {
					state.layer.main_layer = !state.layer.main_layer
				}, 1300)
			}

		},
		//swiperToGame 
		swiperToGame(state,value){
			state.navsGame.forEach(function(nav){
	 			Vue.set(nav,'toggle',false);
	 		});
			Vue.set(state.navsGame[value], 'toggle', true);
			state.swiperGame.slideTo(value,500,false);
		},
		//Game
		showToCenter(state,value){
			  state.swiper.slideTo(value.main, 500, false);
				state.swiperGame.slideTo(value.game,0,false);
				state.navsGame.forEach(function(nav){
		 			Vue.set(nav,'toggle',false);
		 		});
				Vue.set(state.navsGame[value.game],'toggle',true);
				state.headToggle = false;
        ///nav
				state.navs.forEach(function(nav){
					Vue.set(nav,'toggle',false);
				})
				Vue.set(state.navs[1],'toggle',true)
		},
		swiperToCenter(state) {
			state.swiper.slideTo(5, 500, false);
		},
		//
		returnCenter(state) {
			state.swiper.slideTo(3, 500, false);
			state.headToggle = true;
			state.navToggle = true;
			state.centerPage = false;
		},
		//
		ctoggle(state) {
			state.centerPage = true;
			state.headToggle = false;
			state.navToggle = false;
		},
		togglemain(state, value) {
			state.centers.forEach(function(nav) {
					Vue.set(nav, 'toggle', false)
				})
				//
			state.remess = []
				//
			if (value == 1) {
				state.centerTitle = ""
				Vue.set(state.centers[0], "toggle", true)
			}
			if (value == 2) {
				state.centerTitle = ""
				Vue.set(state.centers[1], 'toggle', true)
			}
			//
			if (value == 3) {
				state.centerTitle = ""
				Vue.set(state.centers[2], 'toggle', true)
			}
			//
			if (value == 4) {
				state.centerTitle = ""
				Vue.set(state.centers[3], 'toggle', true)
			}
			//
			if (value == 5 ) {
				state.centerTitle = "";
				Vue.set(state.centers[4], 'toggle', true)
			}
			//

			//
			if (value == 6) {
				state.page = 1; //page
				state.centerTitle = ""
				state.status = true
				Vue.set(state.centers[5], 'toggle', true)
				state.select_1 = ":";
				state.select2 = false; //
				state.recordDatas = []; //
				//
				state.selectData_1 = 0;
				state.recordSelect_1 = [];
				state.recordSelect_1.push({
					text: "",
					value: 0
				}, {
					text: "",
					value: 1
				}, {
					text: "",
					value: 2
				}, {
					text: "",
					value: 3
				})
			}
			//
			if (value == 7) {
				state.page = 1; //page
				state.centerTitle = "";
				state.status = false;
				Vue.set(state.centers[5], 'toggle', true);
				state.records[0].text = "";
				state.recordDatas = [];
				//
				//selectData
				state.selectData_1 = null;
			}
			//
			if (value == 8) {
				state.page = 1; //page
				state.centerTitle = "";
				state.status = true;
				state.select2 = true;
				Vue.set(state.centers[5], 'toggle', true);
				state.select_1 = ":";
				state.select_2 = ":";
				//select_1
				state.selectData_1 = '';
				state.selectData_2 = '';
				//select
				state.recordSelect_1 = [];
				state.recordSelect_1.push(
					{
						text:'',
						value:''
					},{
						text: "AG",
						value: 2
					}, {
						text: "BBIN",
						value: 11
					}, {
						text: "GG",
						value: 12
					}, {
						text: "MG",
						value: 13
					}, {
						text: "PT",
						value: 14
					}, {
						text: "SA",
						value: 15
					})
					//select_2
					state.recordSelect_2.push({
						text: "",
						value:"",
					})
				state.records[0].text = "";
				state.records[1].text = "";
				state.records[2].text = "";
				state.records[3].text = "";
				state.records[4].text = "/";
				state.recordDatas = [];
				//
			}
		},
		//
		register(state) {
			state.paynowToggle=false;
			state.register.regHide = false;
			state.register.regShow = true;
			state.register.contact_toggle=false;
		},
		regbackend(state, value) {
			state.paynowToggle=true;
			state.register.contact_toggle=true;
			if (value = "regiser") {
					state.register.regHide = true;
					state.register.regShow = false;
			}
			if(value = "login"){
					state.register.regHide = true;
					state.register.loginShow = false;
			}
		},
		loginShow(state) {
			state.paynowToggle=false;
			state.register.loginShow = !state.register.loginShow;
			state.register.regHide = !state.register.regHide;
			state.register.contact_toggle=false;
		},
		loginToreg(state) {
			setTimeout(()=>{
				state.register.loginShow = false;
				state.register.regShow = true;
				state.register.contact_toggle=false;
			},100)
		},
		//
		change_select2(state, value) {
			if(value!==''){
				state.recordSelect_2 = [];
			}
			switch (value) {
				case 0:
					state.recordSelect_2.push({
						text: "AG",
						value: 21,
						selected: true
					}, {
						text: "AG",
						value: 22,
						selected: false
					})
					state.recordSelect_2.
					break;
				case 1:
					state.recordSelect_2.push({
						text: "AG",
						value: 21,
						selected: true
					}, {
						text: "AG",
						value: 22,
						selected: false
					})
					break;
				case 2:
					state.recordSelect_2.push({
						text: "AG",
						value: 21,
						selected: true
					}, {
						text: "AG",
						value: 22,
						selected: false
					})
					break;
				case 3:
					state.recordSelect_2.push({
						text: "AG",
						value: 21,
						selected: true
					}, {
						text: "AG",
						value: 22,
						selected: false
					})
					break;
				case 11:
					state.recordSelect_2.push({
						text: "BBIN",
						value: 1
					}, {
						text: "BBIN",
						value: 3

					}, {
						text: "BBIN",
						value: 5,

					}, {
						text: "BBIN",
						value: 12,
					}, {
						text: "BBIN3D",
						value: 15,

					}, {
						text: "BB ",
						value: 54,

					});
					break;
				case 12:
					state.recordSelect_2.push({
						text: "GG ",
						value: 55
					}, {
						text: "GG ",
						value: 56
					});
					break;
				case 13:
					state.recordSelect_2.push({
						text: "MG ",
						value: 90
					}, {
						text: "MG",
						value: 91
					});
					break;
				case 14:
					state.recordSelect_2.push({
						text: "",
						value: 0
					});
					break;
				case 15:
					state.recordSelect_2.push({
						text: "SA ",
						value: 41
					}, {
						text: "SA",
						value: 52
					}, {
						text: "SA",
						value: 53
					});
					break;
			}
			state.selectData_2 = state.recordSelect_2[0].value;
		}
	},
	actions: {
		//
		showWating(context, value) {
			context.commit('ctoggle')
			setTimeout(() => {
				context.commit('swiperToCenter')
			}, 50)
		},
		//
		recordData(context) {
			//record
			setTimeout(() => {
				var url;
				var page = context.state.page++; //
				if (context.state.status == false) {
					var ty = "re_change";
					//
				} else if (context.state.select2 == true) {
					var ty = "re_bet";
					//
				} else if (context.state.select2 == false) {
					var ty = "re_deal";
					//
				}
				switch (ty) {
					case 're_deal':
						url = context.state.url + 'dist/php/mobile/mobile_re_deal.php?i=3.2243';
						break;
					case 're_bet':
						url = context.state.url + 'dist/php/mobile/mobile_re_bet.php?i=3.2243';
						break;
					case 're_change':
						url = context.state.url + 'dist/php/mobile/mobile_re_change.php?i=3.2243';
						break;
				}
				// url
				if (url !== undefined && page <= context.state.totalNumber) {
					//
					context.state.layer.main_layer = true;
					context.state.layer.loging_status = true;
					axios({
						method: "get",
						url: url,
						headers:context.state.register.headers,
						timeout:100000,
						params: {
							username: context.state.register.Globalusername,
							oid: context.state.register.oid,
							ulevel: context.state.register.ulevel,
							page: page,
							t1: context.state.startTime,
							t2: context.state.endTime,
							ty: ty,
							live_game_id:context.state.selectData_1,
							re_deal_code:context.state.selectData_1,
							child_game_id:context.state.selectData_2
						}
					}).then((response) => {
						context.state.layer.main_layer = false;//
						setTimeout(()=>{context.state.layer.loging_status = false;},100);
						if(response.data.dataList==''){
							setTimeout(()=>{context.commit('centerTip', 20);},100);
						}else if(response.data.dataList!==''){
							let data_1 = response.data.data; //
							let data_2 = response.data.dataList; //
							let data_page = response.data.pagation;
							context.state.totalNumber = data_page.totalNumber / 10;
							if (data_page.totalNumber !== 0 && context.state.select2 == false) {
								for (let i = 0; i < data_1.length; i++) {
									context.state.recordDatas.push({
										order: (page * 10 + i+1 - 10),
										number: data_1[i].createTime,
										time: data_1[i].fromKeyType,
										quota: data_1[i].memo,
										winlost: data_1[i].remit
									});
								}
							}else if (data_page.totalNumber !== 0 && context.state.select2 == true) {
								//
								for (let i = 0; i < data_2.length; i++) {
									var arr = new Array();
									switch (data_2[i].winloseType) {
										case "1":
											arr[i] = "";
											break;
										case "2":
											arr[i] = ""
											break;
										case "3":
											arr[i] = ""
									}
									context.state.recordDatas.push({
										order: data_2[i].gameName,
										number: data_2[i].billNo,
										time: data_2[i].betTime,
										quota: data_2[i].betamount,
										winlost: arr[i]
									})
								}

							}else if (data_page.totalNumber == 0) {
								setTimeout(()=>{context.commit('centerTip', 20);},400);
							}
           }
					 //then
				 }).catch((err)=>{
					 //console.log(err);
					 context.commit('centerTip',24);
		       setTimeout(()=>{context.commit('loginout')},1500);
				 })

				} else if (page > context.state.totalNumber) {
					//
					if (a !== 1) {
						context.commit('centerTip', 21);
					} else if (a == 1) {
						return false;
					}
					var a = 1;
				} else {
					context.commit("centerTip", 21)
				}
			},50)
		},
		//
		gameTips(context) {
			switch (context.state.register.loginStatus) {
				//
				case null:
					context.state.layer.main_message = "";
					context.state.layer.main_layer = !context.state.layer.main_layer;
					setTimeout(() => {
						context.state.layer.main_layer = !context.state.layer.main_layer
					}, 1300)
					break
				case 211003:
				  return false;
			}
		},
		loginGame(context,value){
			const loginStatus=context.state.register.loginStatus;
			if(loginStatus===211003&&value!=='mg_game'){
				 window.open(`${context.state.url}dist/php/mobile/mobile_game_login.php?action=${value}&username=${context.state.register.Globalusername}&oid=${context.state.register.oid}`)
			}else if(loginStatus===211003&&value=='mg_game'){
				window.open('/mg.html','_blank');
			}else if(loginStatus===211003&&value=='niuCai'){
 			 window.open('http://47.91.229.32/home/login.html','_banlk');
 		  }else{
			  context.commit('loginTip',4);
		  }

		},
	},
	modules: {
		register: Userregister,
		center: center,
		layer: layer
	}
})

export default userStore;



