<template lang="jade">
.promotion_c
 .title 
 table(border="0")
  thead
   tr
    th 
    th 
    th 
    th 
    th 
  tbody
   tr
    td 200
    td 5
    td 10%
    td 3,000
    td 10
 .title 
 .content
  p 1.2,0001,000 = (2,0001,000) x 10% = 100
  p 2. 
  p 3.10
  p 4.
  p 5.
  p 6.
  p 7.
  info
</template>
<script>
  import info from './info/info.vue';
   export default{
    components:{
       info
    }
  }
</script>
<style lang="scss">
@import "../../assets/css/main.scss";
.promotion_c{
  text-align:left;
  background:#fff;
  padding:{
  	top:rem2px(60);
  	left:rem2px(30);
    right:rem2px(50);
  	bottom:rem2px(50);
  }
  .title{
  	margin-bottom:rem2px(20);
  	color:#b2996c;
  	line-height:rem2px(5);
  	text-align: left;
  	@extend %font24;
  }
  .content{
