    @extend %font16;
    @extend %scale0.9;
    margin-bottom:rem2px(5);
    }
  }
  table{
  	width:100%;
    margin:{
    	top:0.4rem;
    	bottom:0.4rem;
    }
  }
  thead{
  	tr{
  		width:100%;
  		background:#b59c70;
  		color:#fff;
  		 th{
  		  @extend %font16;
        @extend %scaleTable0.8;
  		 	text-align: center;
  		 	line-height:0.55rem;
  		 	border:1px #fff solid;
  		 	font-weight: 200;
  		 }
  	}
  }
  tbody{
  	td{
      @extend %font16;
      @extend %scaleTable0.8;
      text-align: center;
      line-height: 0.45rem;
      border:1px solid #eee;
  	}
  	tr:nth-child(2n){
  		background: #eee;

  	}
  }
}
</style>



