<template>
       	<div class="alipay">
       	   <div class="rewrap">
       	   	  <div class="title">
       	   	  	    <p class="rehead">
       	   	  	    	<span></span>
       	   	  	    	
       	   	  	    </p>
       	   	  	    <v-touch class="rechoice" :class="{'rechoiceh':ctoggle}" @tap="toggle">
                          <!-- <icon name="" ></icon> -->
                        <div class="ralipay"></div>
       	   	  	    	
       	   	  	    </v-touch>
       	   	  </div>
       	   	  <!-- title -->
       	   	  <div class="input">
       	   	  	 <p class="rehead">
       	   	  	    	<span></span>
       	   	  	    	
       	   	  	 </p>
                 <div class="input_l">
                   <input class="t_input" placeholder="" v-model="$store.state.center.chargeMoney" type="text">
                   <input style="display:block;margin-top:0.2rem" type="text" v-model="user_Remark" class='t_input' placeholder="">
                   <div class="input_tip">*  :
                     <span>{{Remark}}</span>
                   </div>
                 </div>
                 <div class="tip_r">
      						 <div class="mesl mesr" >
      	 	   	    	<span ></span>
      	 	   	    	<span > : 10050000<br>
      								  : 105000
      							</span>
      	 	   	     </div>
      	 	   	    <div class="mesl ">
      	 	   	    	<span></span>
      	 	   	    	<span></span>
      	 	   	    </div>
      					 </div>
       	   	  </div>
       	   </div>
              <!-- wrap -->
           <v-touch tag="button" @tap="transform" class="t_btn"></v-touch>
       	</div>
       	<!-- wechat -->
</template>
<script>
    let data ={
    	ctoggle:true,
      Remark:null,
      user_Remark:null
    }
	export default{
		data(){
			return data
		},
    mounted:function(){
      this.getMatch();
    },
		methods:{
			toggle(){
				this.ctoggle=!this.ctoggle;
			},
      getMatch(){
        const mark=Math.floor(Math.random()*100000);
        this.Remark=mark;
      },
      transform(){
        if(this.$store.state.center.chargeMoney==''){
          this.$store.commit("centerTip",4);
        }else if(this.user_Remark==this.Remark&&this.$store.state.center.chargeMoney!==''){
          this.$store.commit("transformPay",{type:2,mark:this.Remark});
          this.user_Remark='';
          this.getMatch();
        }else if(this.user_Remark==null){
            this.$store.commit("centerTip","");
        }else if(this.user_Remark!==this.Remark&&this.user_Remark!==null){
           this.$store.commit("centerTip","");
        }

      }
		}
	}
</script>

<style lang="scss">
@import '../../../assets/css/main.scss';
.ralipay{
      background:url(../../../assets/ailpay.png) no-repeat;
      background-size:contain;
      width:rem2px(30);
      height:rem2px(30);
      display: inline-block;
      position:relative;
      top:0.14rem;
      right:0.1rem;
  }
</style>



