export default {
	state: {
		main_layer: false,
		main_message: null,
		loging_status: false,
		//layer
		binds: [{
			main_bind: false, //
		}, {
			main_bind: false, //
		}, {
			main_bind: false, //
		}, {
			main_bind: false, //email
		}, {
			main_bind: false, //QQ
		}, {
			main_bind: false, //
		}],
		cen_layer: false
	},
	mutations: {
		close_cenlayer(state) {
			state.cen_layer = !state.cen_layer
		},
		//
		bind(state, index) {
			state.cen_layer = !state.cen_layer
			state.binds.forEach(function(item) {
					Vue.set(item, "main_bind", false)
				})
				//
				//
			if (index == 0) {
				state.binds[0].main_bind = true
					//
			} else if (index == 1) {
				state.binds[1].main_bind = true
					//
			} else if (index == 2) {
				state.binds[2].main_bind = true
					//
			} else if (index == 3) {
				state.binds[3].main_bind = true
					//
			} else if (index == 4) {
				state.binds[4].main_bind = true
					//QQ
			} else if (index == 5) {
				state.binds[5].main_bind = true
					//
			}
		},
		loginTip(state, value) {
			if (value == 1) {
				state.main_message = ""
			} else if (value == 2) {
				state.main_message = ""
			} else if (value == 3) {
				state.main_message = ""
			} else if (value == 4) {
				state.main_message = ""
			}
			state.main_layer = !state.main_layer;
			setTimeout(() => {
				state.main_layer = !state.main_layer
			}, 1800)
		},
		registerTip(state, value) {
      state.main_message=value;
			state.main_layer = !state.main_layer;
			setTimeout(() => {
				state.main_layer = !state.main_layer
			}, 1800)
		},
		centerTip(state, value) {
			if (value == 1) {
				state.main_message = "";
			} else if (value == 2) {
				state.main_message = "";
			} else if (value == 3) {
				state.main_message = "";
			} else if (value == 4) {
				state.main_message = "";
			} else if (value == 5) {
				state.main_message = "";
			} else if (value == 6) {
				state.main_message = "";
			} else if (value == 7) {
				state.main_message = "";
			} else if (value == 8) {
				state.main_message = "";
			} else if (value == 9) {
				state.main_message = "";
			} else if (value == 10) {
				state.main_message = "";
			} else if (value == 12) {
				state.main_message = "";
			} else if (value == 13) {
				state.main_message = "";
			} else if (value == 14) {
				state.main_message = "";
			} else if (value == 15) {
				state.main_message = "";
			} else if (value == 16) {
				state.main_message = "";
			} else if (value == 17) {
				state.main_message = ",";
			} else if (value == 18) {
				state.main_message = "";
			} else if (value == 19) {
				state.main_message = "";
			} else if (value == 20) {
				state.main_message = "";
			} else if (value == 21) {
				state.main_message = "";
			} else if (value == 211026){
				state.main_message = "";
			} else if (value == 103047){
				state.main_message = "";
			} else if (value == 113051){
				state.main_message = "";
			} else if (value == 113048){
				state.main_message = "";
			} else if (value == 22) {
				state.main_message = "";
			} else if(value ==103019){
				state.main_message='';
			}else if(value==""){
				state.main_message='';
			}else if(value==""){
				state.main_message='';
			}else if(value==''){
				state.main_message='';
			}else if(value=='23'){
				state.main_message=',';
			}else if(value==24){
				state.main_message='';
			}else if(value==113017){
				state.main_message='!';
			}else if(value==20000){
				state.main_message='';
			}
			state.main_layer = !state.main_layer;
			setTimeout(() => {
				state.main_layer = !state.main_layer
			}, 1800)
		},
		sec_close(state) {
			state.cen_layer = false
		}
	},
}



